body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
  padding: 0 1 rem;
}

.content {
  min-height: calc(100vh - 70px - 64px);
  padding: 1.5rem 0;
}

.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card{
  display: flex;
  flex-direction: column;

}

.card-content{
  flex-grow:1;
}

.cart{
  position:fixed;
  bottom:2rem;
  right: 2rem;
  cursor:pointer;
  z-index:5;
  padding: 1rem;
  display: flex;

}

.cart .material-icons{
  width:50px;
}


.basket-list{
  position:fixed;
  top:50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 50 rem;
  max-width: 95%;
  animation: show 750ms ease-in-out;
}


.basket-close{
  cursor: pointer;
  position:absolute;
  top:10px;
  right:0.5rem;
  color:white;
}

.basket-delete{
  cursor: pointer;
}


.basket-quantity{
  cursor: pointer;
  vertical-align:middle;
  color: #26a69a;
}


#toast-container{
  top: 3rem !important;
  animation: toast-fade-in 500ms ease-in-out;
}

/* ANIMATIONS */

@keyframes show {
  from {
    top:70%;
    opacity: 0;
  }
  to{
    top:50%;
    opacity: 1;
  }
}

@keyframes toast-fade-in{
  from{
    opacity: 0;
    transform: translateY(2rem)
  }

  to{
    opacity: 1;
    transform: translateY(0rem)
  }
  
}